.Categories-heading{
    font: normal normal 800 22px/31px Gilroy;
    letter-spacing: 0.5px;
    color: #3C4852;
    opacity: 1;
}

.ListOfCourses > *{
    font: normal normal normal 18px/26px Gilroy;
    letter-spacing: 0px;
    color: #3C4852;
    display:block;
    margin-bottom: 20px;
}



.active-category{
    width:100%;
    background: #08BD80 0% 0% no-repeat padding-box !important;
    border-radius: 8px;
    opacity: 1;
    padding:4px;
    padding-left:6px;
    font: normal normal normal 18px/26px Gilroy;
    letter-spacing: 0px;
    color: white;
    opacity: 1;

}
.ListOfCourses:hover{
    color:white!important;
}
.ListOfCourses a:hover{
    text-decoration:none;
    
}




/* Cards section */
.CategoryDevider{
    display:flex;
}


.Course-Cards{
    display:flex;
    flex-wrap:wrap;
    margin-left:0px;
    margin-bottom: 40px;

    
}

.CategoriesTitle{
    margin-left:30px;
}

.my-card{
    margin-left:20px;
    display:flex;
    flex-direction: column;

}

.CategoriesTitle{
    font: normal normal  30px/38px Gilroy;
    letter-spacing: 0px;
    color: #3C4852;
    opacity: 1;
    
}

.my-card img{
    width:290px;
    height:160px;
    border-radius:8px;

}

.Course-Devider{
   height:100vh;
   border: 1px solid #707070;
   opacity: 0.5;
   margin-left:20px;
}

.Course-Title{
    padding-top:12px;
    font: normal normal normal 18px/14px Gilroy;
    letter-spacing: 0px;
    color: #3C4852;
}


.Course-Teacher,.Course-info{
    color: #3C4852;
    font: normal normal 17px/19px Gilroy;
    letter-spacing: 0px;
    color: #3C4852;
    
}

.Course-Teacher{
    font-size:14px;
    opacity:0.9;
    line-height:0px;
    
}

.Course-info{
    font-size:14px;
    display: flex;
    
    
}

.Course-rating{
   
    text-align:left;
}

.Course-star{
 
    
    flex-grow:0!important;
 
    margin-left:4px;
}

.CourseTimesUpdated{
    flex-grow:1;
    text-align:left;
    font-size:14px;
    opacity:0.9;

}

.Course-Price{

    flex-grow:1;
    text-align: right;
}

.recommended{

    border-radius: 8px;
    padding-left:4px;
    background:rgb(66, 66, 133)!important;
    color:white;
}

@media(max-width:992px){
    .Categories-main{
       display:none;
    }
    .Course-Devider{
        display: none;
    }
    
    .CategoriesTitle{
        text-align: center;
    }

    .Course-Cards{
        margin-left:20px;
        margin-bottom: 40px;
        
    }
}

@media(max-width:440px){

    .my-card img{
        width:250px;
        height:120px;
        border-radius:8px;
        margin:0px;

    }
    .my-card{
        margin:0px;
    }
    .CategoriesTitle{
        font: normal normal  20px/38px Gilroy;
       
        
    }
}